<template>
    <tab :line-width=2 active-color='#d9261a' :index.sync="index" class="jq-test">
      <tab-item class="vux-center" :selected="demo === item"  v-for="item in tablist" @click="tabChange(item)">{{item}}</tab-item>
    </tab>

  <swiper :index.sync="index"  height="10rem" :show-dots="false" class="clearfix jq-te">
    <swiper-item class='jq-bu'>
      <div  class='jq-so'>

            <group class="jq-mt">
             <popup-picker :title="datetile" :data="timelist2" :value.sync="timeEffectiveness"></popup-picker>
           </group>
            <group class="jq-mt" v-if='indexDep>=1'>
              <div class="chart" v-ebar="departmentOne"></div>
             </group>
           <group class="jq-mt" v-if='indexDep>=2'>
              <div class="chart" v-ebar="departmentTwo"></div>
           </group>
          <group class="jq-mt" v-if='indexDep>=3'>
              <div class="chart" v-ebar="departmentThree"></div>
          </group>
          <group class="jq-mt" v-if='indexDep>=4'>
              <div class="chart" v-ebar="departmentFour"></div>
          </group>
          <group class="jq-mt" v-if='indexDep>=5'>
              <div class="chart" v-ebar="departmentFive"></div>
          </group>
      </div>
    </swiper-item>
    <!--退货率-->
    <swiper-item >
        <!--<div  v-if="index==1">-->
           <group class="jq-mt">
          <popup-picker :title="datetile" :data="timelist2" :value.sync="time"></popup-picker>
           </group>
          <group class="jq-mt">
          <div class="chart" v-ebar="barDataReturnRate"></div>
          </group>
      <!--</div>-->
    </swiper-item>
    <!--出勤率-->
    <swiper-item >
      <!--<div  v-if="index==2">-->
          <group class="jq-mt">
           <popup-picker :title="datetile" :data="timelist" :value.sync="timeAttendance"   ></popup-picker>
             </group>
        <group class="jq-mt">
     <div class="chart" v-ebar="barDataAttendance"></div>
      </group>
        <group class="jq-mt">
     <div class="chart" v-ebar="barDataLossRate"></div>
      </group>
    <!--</div>-->
    </swiper-item>
  </swiper>


</template>
  <script>
    import {Group,Selector,Tab, TabItem,Swiper,SwiperItem,Cell,Scroller,sticky,PopupPicker} from 'vux/src/components'
    import { getDept,getList } from '../../../untils/api'
    const list = () => ['人均小时效率', '退货率','出勤率']
    export default {
    components: {
    Group,
    Selector,
    Tab,
    TabItem,
    Swiper,
    SwiperItem,
    PopupPicker,
    Cell,
    Scroller,
    sticky
    },
    data () {
    return {
    datetile:'日期',
    demo: '人均小时效率',
    tablist:list(),
    time: formatData.defaultTimeYear,
    timeEffectiveness: formatData.defaultTimeYear,
    timeAttendance:formatData.defaultTime,
    timelist: formatData.dateList,
    timelist2:formatData.dateListYear,
    busList: formatData.busList,
    departmentList:[],
    departmentOne:{},
    departmentTwo:{},
    departmentThree:{},
    departmentFour:{},
    departmentFive:{},
    indexDep:0,
    barDataReturnRate:{},//存仓
    barDataAttendance:{},//出勤率
    barDataLossRate:{},//流失率
    index:0,
    isClickWarehousing:0,
    isClickAttendance:0,
    }
    },
    ready(){
       document.title='生产制造';
    },
    methods: {
    tabChange(item){
    if(item=='退货率')//退货
    {
      if(this.isClickWarehousing==0){
        this.getDataReturnRate();
        this.isClickWarehousing++;
      }

    }else if(item=='出勤率'){//出勤率
      if(this.isClickAttendance==0){
        this.getbarDataAttendance();
        this.getbarDataLossRate();
        this.isClickAttendance++;
      }
    }

    },

    async getDepartmentList() {
    commit('UPDATE_LOADING', true)
    let id ='APP10207c';

    var date=formatData.dateConversion(this.timeEffectiveness);
    let dataTit = await getDept('ADD');
    if(dataTit.success){
     let list=dataTit.data;
       if(list.length>0){
         this.indexDep=list.length;
         console.log(this.indexDep);
          for(let i=0;i<list.length;i++){
            let deptId=list[i].deptId;
             if(i==0){
                let dataTit = await getList(id,{date_:date,DEPT_CH:deptId});
                if(dataTit.success){

                 const TableList = dataTit.data;
                  TableList.workDescribe='ADD'+list[i].deptName+TableList.workDescribe;
                 this.departmentOne = {};
                 this.departmentOne.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
                if(i==this.indexDep-1){
                   commit('UPDATE_LOADING', false)
                }
                }
             }
              if(i==1){
                let dataTit = await getList(id,{date_:date,DEPT_CH:deptId});
                if(dataTit.success){
                 //表头：
                 //表格:
                 const TableList = dataTit.data;
                  TableList.workDescribe='ADD'+list[i].deptName+TableList.workDescribe;
                 this.departmentTwo = {};
                 this.departmentTwo.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
                 if(i==this.indexDep-1){
                   commit('UPDATE_LOADING', false)
                 }
              }
            }
           if(i==2){
                let dataTit = await getList(id,{date_:date,DEPT_CH:deptId});
                if(dataTit.success){
                 //表头：
                 //表格:
                 const TableList = dataTit.data;
                  TableList.workDescribe='ADD'+list[i].deptName+TableList.workDescribe;
                 this.departmentThree = {};
                 this.departmentThree.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
                 if(i==this.indexDep-1){
                   commit('UPDATE_LOADING', false)
                 }
              }

                if(i==this.indexDep-1){
                 commit('UPDATE_LOADING', false)
                }
             }

              if(i==3){
               let dataTit = await getList(id,{date_:date,DEPT_CH:deptId});
                if(dataTit.success){
                 //表头：
                 //表格:
                 const TableList = dataTit.data;
                  TableList.workDescribe='ADD'+list[i].deptName+TableList.workDescribe;
                 this.departmentFour = {};
                 this.departmentFour.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
                 if(i==this.indexDep-1){
                   commit('UPDATE_LOADING', false)
                 }
              }

                if(i==this.indexDep-1){
                 commit('UPDATE_LOADING', false)
                }
             }
              if(i==4){
                let dataTit = await getList(id,{date_:date,DEPT_CH:deptId});
                if(dataTit.success){
                 //表头：
                 //表格:
                 const TableList = dataTit.data;
                  TableList.workDescribe='ADD'+list[i].deptName+TableList.workDescribe;
                 this.departmentFive = {};
                 this.departmentFive.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
                 if(i==this.indexDep-1){
                   commit('UPDATE_LOADING', false)
                 }
              }

                if(i==this.indexDep-1){
                 commit('UPDATE_LOADING', false)
                }
             }


         }
       }
    }

    else{
      commit('UPDATE_LOADING', false)
     console.log(dataTit.message);
    }

    },

    async getDataReturnRate() {
        commit('UPDATE_LOADING', true)
    let id ='APP10208';
    var date=formatData.dateConversion(this.timeEffectiveness);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
   
    //表头：
    //表格:
    const TableList = dataTit.data;
   
    this.barDataReturnRate = {};
    this.barDataReturnRate.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    },
     async getbarDataAttendance() {
         commit('UPDATE_LOADING', true)
    let id ='APP10209c';
    var date=formatData.dateConversion(this.timeAttendance);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
   
    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barDataAttendance = {};
    this.barDataAttendance.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    },
     async getbarDataLossRate() {
         commit('UPDATE_LOADING', true)
    let id ='APP10210c';
     var date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
    
    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barDataLossRate = {};
    this.barDataLossRate.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    }
    },
      watch: {
    index (newIndex, oldIndex) {
     if(this.index==1)//退货
    {
      if(this.isClickWarehousing==0){
        this.getDataReturnRate();
        this.isClickWarehousing++;
      }

    }else if(this.index==2){//出勤率
      if(this.isClickAttendance==0){
        this.getbarDataAttendance();
        this.getbarDataLossRate();
        this.isClickAttendance++;
      }
    }
    },
    timeEffectiveness(newdate,olddate){
          this.getDepartmentList();
          formatData.setDateTime(newdate);
    },
    time(newdate,olddate){
       this.getDataReturnRate();
        formatData.setDateTime(newdate);
    },
     timeAttendance(newdate,olddate){
       this.getbarDataAttendance();
       this.getbarDataLossRate();
        formatData.setDateTime(newdate);
    }
  },
    created: function () {
      this.departmentOne={};
      this.departmentOne.dataset=[];

      this.departmentTwo={};
      this.departmentTwo.dataset=[];

      this.departmentThree={};
      this.departmentThree.dataset=[];

      this.departmentFour={};
      this.departmentFour.dataset=[];

      this.departmentFive={};
      this.departmentFive.dataset=[];

      this.barDataReturnRate={};
      this.barDataReturnRate.dataset = [];

      this.barDataAttendance={};
      this.barDataAttendance.dataset=[];
      this.barDataLossRate={};
      this.barDataLossRate.dataset=[];

      this.getDepartmentList();
    }
    }
  </script>






